<template>
  <div class="topic-selection-container">
    <el-card class="topic-card" shadow="hover">
      <div slot="header" class="clearfix">
        <h2>智能选题助手</h2>
      </div>

      <el-form ref="form" :model="form" label-width="80px" class="topic-form">
        <el-form-item label="领域关键词">
          <el-input v-model="form.keyword" placeholder="请输入内容领域，如'美食'、'旅行'" @keyup.enter.native="generateTopics"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="generateTopics" :loading="loading">生成选题</el-button>
          <el-button @click="refreshTopics" :disabled="!topics.length">刷新选题</el-button>
          <el-select v-model="difficulty" placeholder="选择难度" class="difficulty-select">
            <el-option label="简单" value="easy"></el-option>
            <el-option label="中等" value="medium"></el-option>
            <el-option label="困难" value="hard"></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <div v-if="topics.length > 0" class="topics-result">
        <h3>为您推荐的热点选题：</h3>
        <el-list
          v-for="(topic, index) in topics"
          :key="index"
          class="topic-item"
          border
          :disabled="false"
          @click="goToEditor(index)">
          <el-list-item>
            <el-icon slot="prefix"><fire-filled /></el-icon>
            {{ topic }}
          </el-list-item>
        </el-list>
      </div>

      <div v-else-if="showNoResult" class="no-result">
        <el-empty description="暂无选题，请输入关键词并点击生成"></el-empty>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { FireFilled } from '@element-plus/icons-vue'
import axios from 'axios'

const router = useRouter()
const form = reactive({
  keyword: ''
})
const topics = ref([])
const loading = ref(false)
const showNoResult = ref(false)
const difficulty = ref('medium')

// 生成选题
const generateTopics = async () => {
  if (!form.keyword.trim()) {
    ElMessage.warning('请输入领域关键词')
    return
  }

  loading.value = true
  try {
    // 从本地JSON文件获取模拟数据
    const response = await axios.get('/mock/topics.json')
    const topicData = response.data
    const lowerKeyword = form.keyword.toLowerCase()

    // 查找匹配的领域选题
    let matchedTopics = []
    for (const [domain, domainTopics] of Object.entries(topicData)) {
      if (domain.includes(lowerKeyword)) {
        matchedTopics = [...matchedTopics, ...domainTopics]
      }
    }

    // 如果没有精确匹配，使用所有领域的选题
    if (matchedTopics.length === 0) {
      for (const domainTopics of Object.values(topicData)) {
        matchedTopics = [...matchedTopics, ...domainTopics]
      }
    }

    // 根据难度筛选选题数量
    let topicCount = 5
    if (difficulty.value === 'easy') topicCount = 10
    if (difficulty.value === 'hard') topicCount = 3

    // 随机选择指定数量的选题
    topics.value = matchedTopics
      .sort(() => 0.5 - Math.random())
      .slice(0, topicCount)

    showNoResult.value = topics.value.length === 0
  } catch (error) {
    ElMessage.error('获取选题失败，请重试')
    console.error('Error fetching topics:', error)
  } finally {
    loading.value = false
  }
}

// 刷新选题
const refreshTopics = () => {
  if (form.keyword.trim()) {
    generateTopics()
  }
}

// 跳转到内容编辑器
const goToEditor = (topicIndex) => {
  router.push({
    name: 'ContentEditor',
    params: {
      topicId: topicIndex,
      topicTitle: topics.value[topicIndex]
    }
  })
}

onMounted(() => {
  // 页面加载时默认显示一些选题
  form.keyword = '美食'
  generateTopics()
})
</script>

<style scoped>
.topic-selection-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 0 20px;
}

.topic-card {
  border-radius: 12px;
}

.topic-form {
  margin-bottom: 30px;
}

.difficulty-select {
  margin-left: 15px;
  width: 120px;
}

.topics-result {
  margin-top: 20px;
}

.topic-item {
  margin-bottom: 10px;
  cursor: pointer;
  transition: all 0.3s;
}

.topic-item:hover {
  background-color: #f5f7fa;
}

.no-result {
  margin-top: 40px;
}
</style>