<template>
  <div class="dashboard-container">
    <el-card shadow="hover">
      <div slot="header" class="clearfix">
        <el-button @click="goBack" icon="ArrowLeft" class="back-button">返回编辑</el-button>
        <h2 class="dashboard-title">内容数据看板</h2>
      </div>

      <div class="dashboard-content">
        <div class="statistics-card">
          <el-card class="stat-card">
            <div class="stat-item">
              <div class="stat-label">总阅读量</div>
              <div class="stat-value">{{ stats.totalReads }}</div>
              <div class="stat-trend" :class="stats.readsTrend > 0 ? 'up' : 'down'">{{ stats.readsTrend > 0 ? '+' : '' }}{{ stats.readsTrend }}%</div>
            </div>
          </el-card>

          <el-card class="stat-card">
            <div class="stat-item">
              <div class="stat-label">平均点赞率</div>
              <div class="stat-value">{{ stats.avgLikesRate }}%</div>
              <div class="stat-trend" :class="stats.likesTrend > 0 ? 'up' : 'down'">{{ stats.likesTrend > 0 ? '+' : '' }}{{ stats.likesTrend }}%</div>
            </div>
          </el-card>

          <el-card class="stat-card">
            <div class="stat-item">
              <div class="stat-label">转发分享数</div>
              <div class="stat-value">{{ stats.shares }}</div>
              <div class="stat-trend" :class="stats.sharesTrend > 0 ? 'up' : 'down'">{{ stats.sharesTrend > 0 ? '+' : '' }}{{ stats.sharesTrend }}%</div>
            </div>
          </el-card>

          <el-card class="stat-card">
            <div class="stat-item">
              <div class="stat-label">评论数</div>
              <div class="stat-value">{{ stats.comments }}</div>
              <div class="stat-trend" :class="stats.commentsTrend > 0 ? 'up' : 'down'">{{ stats.commentsTrend > 0 ? '+' : '' }}{{ stats.commentsTrend }}%</div>
            </div>
          </el-card>
        </div>

        <div class="charts-container">
          <el-card class="chart-card">
            <div slot="header">
              <h3>3天流量趋势</h3>
            </div>
            <ECharts :option="trafficOption" class="chart" />
          </el-card>

          <el-card class="chart-card">
            <div slot="header">
              <h3>用户互动分析</h3>
            </div>
            <ECharts :option="interactionOption" class="chart" />
          </el-card>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { ArrowLeft } from '@element-plus/icons-vue'
import axios from 'axios'

const router = useRouter()
const stats = reactive({
  totalReads: 0,
  readsTrend: 0,
  avgLikesRate: 0,
  likesTrend: 0,
  shares: 0,
  sharesTrend: 0,
  comments: 0,
  commentsTrend: 0
})
const trafficOption = ref({})
const interactionOption = ref({})

// 返回内容编辑页
const goBack = () => {
  router.push('/editor/0')
}

// 初始化图表数据
const initCharts = (data) => {
  // 流量趋势图表
  trafficOption.value = {
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow'
      }
    },
    legend: {
      data: ['阅读量', '点赞数', '转发数']
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      data: data.traffic.dates
    },
    yAxis: {
      type: 'value'
    },
    series: [
      {
        name: '阅读量',
        type: 'line',
        data: data.traffic.reads,
        smooth: true,
        itemStyle: {
          color: '#409EFF'
        }
      },
      {
        name: '点赞数',
        type: 'line',
        data: data.traffic.likes,
        smooth: true,
        itemStyle: {
          color: '#67C23A'
        }
      },
      {
        name: '转发数',
        type: 'line',
        data: data.traffic.shares,
        smooth: true,
        itemStyle: {
          color: '#E6A23C'
        }
      }
    ]
  }

  // 用户互动分析图表
  interactionOption.value = {
    tooltip: {
      trigger: 'item'
    },
    legend: {
      orient: 'vertical',
      left: 'left'
    },
    series: [
      {
        name: '互动类型',
        type: 'pie',
        radius: '60%',
        data: [
          {
            value: data.interaction.likes,
            name: '点赞'
          },
          {
            value: data.interaction.comments,
            name: '评论'
          },
          {
            value: data.interaction.shares,
            name: '转发'
          },
          {
            value: data.interaction.saves,
            name: '收藏'
          }
        ],
        emphasis: {
          itemStyle: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      }
    ]
  }
}

// 加载模拟数据
const loadMockData = async () => {
  try {
    const response = await axios.get('/mock/dashboard-data.json')
    const data = response.data

    // 更新统计数据
    stats.totalReads = data.statistics.totalReads
    stats.readsTrend = data.statistics.readsTrend
    stats.avgLikesRate = data.statistics.avgLikesRate
    stats.likesTrend = data.statistics.likesTrend
    stats.shares = data.statistics.shares
    stats.sharesTrend = data.statistics.sharesTrend
    stats.comments = data.statistics.comments
    stats.commentsTrend = data.statistics.commentsTrend

    // 初始化图表
    initCharts(data)
  } catch (error) {
    ElMessage.error('加载数据失败，请重试')
    console.error('Error loading dashboard data:', error)
  }
}

onMounted(() => {
  // 页面加载时加载数据
  loadMockData()
})
</script>

