<template>
  <div class="content-editor-container">
    <el-card shadow="hover">
      <div slot="header" class="clearfix">
        <el-button @click="goBack" icon="ArrowLeft" class="back-button">返回选题</el-button>
        <h2 class="editor-title">内容编辑器</h2>
      </div>

      <el-form ref="form" :model="form" class="content-form">
        <el-form-item label="标题">
          <el-input v-model="form.title" placeholder="请输入内容标题" class="title-input"></el-input>
        </el-form-item>

        <el-form-item label="平台类型">
          <el-radio-group v-model="platformType" @change="convertContent">
            <el-radio-button label="default">默认格式</el-radio-button>
            <el-radio-button label="wechat">公众号</el-radio-button>
            <el-radio-button label="douyin">抖音脚本</el-radio-button>
            <el-radio-button label="xiaohongshu">小红书</el-radio-button>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="内容编辑">
          <quill-editor
            v-model:content="form.content"
            class="editor"
            :options="editorOptions"
          />
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="saveContent">保存内容</el-button>
          <el-button @click="generateContent">重新生成</el-button>
          <el-button @click="analyzeContent" class="ml-2">内容分析</el-button>
          <el-button @click="goToDashboard" class="ml-2" type="success">查看数据</el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!-- 内容分析结果弹窗 -->
    <el-dialog v-model="analysisDialogVisible" title="内容优化建议" width="600px">
      <div v-for="(suggestion, index) in analysisSuggestions" :key="index" class="suggestion-item">
        <el-alert
          :title="suggestion.title"
          :description="suggestion.content"
          :type="suggestion.type"
          show-icon
        ></el-alert>
      </div>
      <template #footer>
        <el-button @click="analysisDialogVisible = false">关闭</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ArrowLeft } from '@element-plus/icons-vue'
import axios from 'axios'

const route = useRoute()
const router = useRouter()
const form = reactive({
  title: '',
  content: ''
})
const platformType = ref('default')
const analysisDialogVisible = ref(false)
const analysisSuggestions = ref([])

// 富文本编辑器配置
const editorOptions = {
  theme: 'snow',
  modules: {
    toolbar: [
      ['bold', 'italic', 'underline', 'strike'],
      [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
      [{ 'list': 'ordered'}, { 'list': 'bullet' }],
      [{ 'align': [] }],
      ['link', 'image'],
      ['clean']
    ]
  }
}

// 返回选题页面
const goBack = () => {
  router.push('/')
}

// 跳转到数据看板
const goToDashboard = () => {
  router.push('/dashboard')
}

// 生成内容
const generateContent = () => {
  const topicTitle = route.params.topicTitle || '未选择选题'
  form.title = topicTitle

  // 模拟LLM生成内容框架
  const templates = [
    {
      paragraphs: [
        `据最新行业报告显示，{{topic}}相关内容的关注度同比增长了{{percentage}}%，成为当下最热门的话题之一。本文将为您详细解析{{topic}}的核心要点和实践方法。`,
        `在{{场景1}}中，{{topic}}的应用可以带来显著效果。例如，{{案例1}}。这一方法不仅适用于{{场景1}}，在{{场景2}}中同样表现出色。`,
        `想要做好{{topic}}，需要注意以下几点：首先，{{要点1}}；其次，{{要点2}}；最后，{{要点3}}。掌握这些技巧，您将在{{领域}}领域脱颖而出。`,
        `现在就开始尝试{{topic}}吧！无论是{{适用人群1}}还是{{适用人群2}}，都能从中获益。欢迎在评论区分享您的经验和想法！`
      ]
    },
    {
      paragraphs: [
        `您是否正在寻找关于{{topic}}的实用指南？本文将为您提供一站式解决方案，从基础概念到高级技巧，助您快速掌握{{topic}}的精髓。`,
        `首先，让我们了解{{topic}}的基本原理。{{原理解释}}。理解这一点后，我们再来看看实际应用中的关键步骤：{{步骤1}}、{{步骤2}}、{{步骤3}}。`,
        `以下是几个成功案例：{{案例1}}和{{案例2}}。这些案例展示了{{topic}}在不同场景下的应用效果，值得我们借鉴。`,
        `最后，为您总结{{topic}}的核心要点和注意事项。记住，{{关键提示}}。希望本文能帮助您在{{领域}}领域取得更好的成绩！`
      ]
    }
  ]

  // 随机变量池
  const variables = {
    percentage: [23, 45, 67, 89, 120],
   场景1: ['日常生活', '工作场合', '学习过程', '社交活动'],
   场景2: ['商业推广', '个人成长', '团队协作', '创意设计'],
    案例1: ['某知名博主通过该方法获得了10万+点赞', '许多用户反馈使用后效率提升50%', '行业专家推荐的首选方案'],
    要点1: ['明确目标受众', '制定详细计划', '收集相关资料', '保持创新思维'],
    要点2: ['注重细节处理', '持续优化改进', '与他人交流合作', '跟踪最新趋势'],
    要点3: ['定期总结经验', '勇于尝试新方法', '建立反馈机制', '保持耐心和毅力'],
    领域: ['自媒体', '内容创作', '个人品牌', '数字营销'],
    适用人群1: ['初学者', '职场新人', '内容创作者'],
    适用人群2: ['营销人员', '企业管理者', '自由职业者'],
    原理解释: ['这是一种基于数据驱动的内容创作方法', '通过分析用户需求和热点趋势来制定内容策略', '结合心理学原理提高内容的吸引力和传播力'],
    步骤1: ['确定主题方向', '进行市场调研', '制定内容大纲'],
    步骤2: ['创作初稿内容', '优化标题和封面', '添加关键词'],
    步骤3: ['测试内容效果', '收集用户反馈', '进行迭代优化'],
    案例2: ['某企业利用该策略提升了30%的转化率', '新手创作者通过此方法在1个月内获得1000+粉丝'],
    关键提示: ['内容质量是核心，切勿忽视原创性', '持续学习和适应新趋势是成功的关键', '与受众建立良好互动能有效提升内容影响力']
  }

  // 随机选择一个模板
  const template = templates[Math.floor(Math.random() * templates.length)]

  // 生成内容并替换变量
  let content = ''
  template.paragraphs.forEach(paragraph => {
    let processedParagraph = paragraph
    // 替换所有变量
    Object.keys(variables).forEach(key => {
      if (processedParagraph.includes(`{{${key}}}`)) {
        const randomValue = variables[key][Math.floor(Math.random() * variables[key].length)]
        processedParagraph = processedParagraph.replace(`{{${key}}}`, randomValue)
      }
    })
    content += `<p>${processedParagraph}</p>`
  })

  form.content = content
  // 应用当前平台格式
  convertContent()
}

// 转换内容格式
const convertContent = () => {
  if (!form.content) return

  let processedContent = form.content

  switch(platformType.value) {
    case 'wechat':
      // 公众号格式：添加关注引导
      processedContent = form.content + '<p><strong>点击关注获取更多干货～</strong></p>'
      break
    case 'douyin':
      // 抖音脚本格式：添加标记
      processedContent = form.content
        .replace(/<p>/g, '<p>[字幕]')
        .replace(/<\/p>/g, '</p>\n[转场]')
        .replace(/<strong>(.*?)<\/strong>/g, '[重点]$1[/重点]')
      break
    case 'xiaohongshu':
      // 小红书格式：添加emoji和话题标签
      const topicTags = form.title
        .split(/[，。 、]/)
        .filter(word => word.length > 2)
        .slice(0, 3)
        .map(word => `#${word}`)
        .join(' ')

      processedContent = form.content
        .replace(/<p>/g, '<p>✨ ')
        .replace(/<\/p>/g, '</p>')
        + `<p>${topicTags}</p>`
      break
    default:
      // 默认格式不做处理
      processedContent = form.content
  }

  form.content = processedContent
}

// 保存内容
const saveContent = () => {
  if (!form.title.trim() || !form.content.trim()) {
    ElMessage.warning('标题和内容不能为空')
    return
  }

  // 这里可以添加保存到本地存储或提交到后端的逻辑
  ElMessage.success('内容保存成功')
}

// 分析内容并生成优化建议
const analyzeContent = () => {
  if (!form.title.trim() || !form.content.trim()) {
    ElMessage.warning('请先生成或编辑内容')
    return
  }

  analysisSuggestions.value = []

  // 模拟内容分析规则
  const rules = [
    {
      check: (title) => title.includes('数字'),
      suggestion: {
        title: '标题含数字提升点击量',
        content: '您的标题已包含数字，这有助于提高20%的点击量。',
        type: 'success'
      },
      oppositeSuggestion: {
        title: '建议标题添加数字',
        content: '研究表明，标题中包含数字可以提升20%的点击量，建议添加如"5个技巧"、"3种方法"等。',
        type: 'warning'
      }
    },
    {
      check: (content) => content.split('</p>').length <= 5,
      suggestion: {
        title: '内容长度适中',
        content: '您的内容段落数适中，易于阅读。',
        type: 'success'
      },
      oppositeSuggestion: {
        title: '建议精简内容',
        content: '内容段落较多，建议控制在5段以内，每段不超过3行，提升阅读体验。',
        type: 'warning'
      }
    },
    {
      check: (content) => content.includes('案例') || content.includes('例子'),
      suggestion: {
        title: '包含实际案例',
        content: '内容中包含实际案例，有助于读者理解和应用。',
        type: 'success'
      },
      oppositeSuggestion: {
        title: '建议添加案例',
        content: '添加实际案例或例子可以使内容更具说服力和实用性。',
        type: 'info'
      }
    },
    {
      check: (title) => title.length <= 20,
      suggestion: {
        title: '标题长度合适',
        content: '标题长度适中，便于在各种平台展示完整。',
        type: 'success'
      },
      oppositeSuggestion: {
        title: '建议缩短标题',
        content: '标题过长，建议控制在20字以内，提高可读性和吸引力。',
        type: 'warning'
      }
    }
  ]

  // 应用分析规则
  rules.forEach(rule => {
    if (rule.check(form.title || form.content)) {
      analysisSuggestions.value.push(rule.suggestion)
    } else if (rule.oppositeSuggestion) {
      analysisSuggestions.value.push(rule.oppositeSuggestion)
    }
  })

  // 打开分析结果弹窗
  analysisDialogVisible.value = true
}

onMounted(() => {
  // 页面加载时生成内容
  generateContent()
})
</script>

<style scoped>
.content-editor-container {
  max-width: 1000px;
  margin: 20px auto;
  padding: 0 20px;
}

.back-button {
  margin-right: 15px;
}

.editor-title {
  display: inline-block;
}

.content-form {
  margin-top: 20px;
}

.title-input {
  font-size: 18px;
  height: 50px;
}

.editor {
  height: 400px;
  margin-bottom: 20px;
}

.suggestion-item {
  margin-bottom: 15px;
}
</style>