<template>
  <div id="app">
    <el-container class="app-container">
      <el-header class="app-header">
        <div class="logo">智能自媒体内容创作平台</div>
        <el-menu :default-active="activeIndex" mode="horizontal" class="header-menu">
          <el-menu-item index="1" @click="goToHome">选题助手</el-menu-item>
          <el-menu-item index="2" @click="goToDashboard">数据看板</el-menu-item>
        </el-menu>
      </el-header>
      <el-main class="app-main">
        <router-view />
      </el-main>
      <el-footer class="app-footer">
        智能自媒体内容创作与运营平台 ©2025
      </el-footer>
    </el-container>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { useRouter, useRoute } from 'vue-router'

const router = useRouter()
const route = useRoute()
const activeIndex = ref('1')

// 跳转到首页
const goToHome = () => {
  router.push('/')
}

// 跳转到数据看板
const goToDashboard = () => {
  router.push('/dashboard')
}

onMounted(() => {
  // 根据当前路由设置激活的菜单项
  if (route.path === '/dashboard') {
    activeIndex.value = '2'
  } else {
    activeIndex.value = '1'
  }

  // 监听路由变化更新菜单项
  router.beforeEach((to) => {
    activeIndex.value = to.path === '/dashboard' ? '2' : '1'
  })
})
</script>

